	module.exports = {"data":{"challengeNode":{"title":"Pig Latin","description":["在这道题目中，我们需要写一个函数，把传入的字符串翻译成“儿童黑话”。","<a href=\"http://en.wikipedia.org/wiki/Pig_Latin\" target=\"_blank\">儿童黑话</a>的基本转换规则很简单，只需要把一个英文单词的第一个辅音字母或第一组辅音从移到单词的结尾，并在后面加上<code>ay</code>即可。在英语中，字母 a、e、i、o、u 为元音，其余的字母均为辅音。辅音从的意思是连续的多个辅音字母。","额外地，如果单词本身是以元音开头的，那只需要在结尾加上<code>way</code>。","在本题中，传入的单词一定会是英文单词，且所有字母均为小写。","如果你遇到了问题，请点击<a href='https://forum.freecodecamp.one/t/topic/157' target='_blank'>帮助</a>。"],"challengeType":5,"fields":{"slug":"/javascript-algorithms-and-data-structures/intermediate-algorithm-scripting/pig-latin","blockName":"Intermediate Algorithm Scripting","tests":[{"text":"<code>translatePigLatin(\"california\")</code>应该返回 \"aliforniacay\"。","testString":"assert.deepEqual(translatePigLatin(\"california\"), \"aliforniacay\", '<code>translatePigLatin(\"california\")</code>应该返回 \"aliforniacay\"。');"},{"text":"<code>translatePigLatin(\"paragraphs\")</code>应该返回 \"aragraphspay\"。","testString":"assert.deepEqual(translatePigLatin(\"paragraphs\"), \"aragraphspay\", '<code>translatePigLatin(\"paragraphs\")</code>应该返回 \"aragraphspay\"。');"},{"text":"<code>translatePigLatin(\"glove\")</code>应该返回 \"oveglay\"。","testString":"assert.deepEqual(translatePigLatin(\"glove\"), \"oveglay\", '<code>translatePigLatin(\"glove\")</code>应该返回 \"oveglay\"。');"},{"text":"<code>translatePigLatin(\"algorithm\")</code>应该返回 \"algorithmway\"。","testString":"assert.deepEqual(translatePigLatin(\"algorithm\"), \"algorithmway\", '<code>translatePigLatin(\"algorithm\")</code>应该返回 \"algorithmway\"。');"},{"text":"<code>translatePigLatin(\"eight\")</code>应该返回 \"eightway\"。","testString":"assert.deepEqual(translatePigLatin(\"eight\"), \"eightway\", '<code>translatePigLatin(\"eight\")</code>应该返回 \"eightway\"。');"},{"text":"你的代码应当能够处理第一个元音字母在单词结尾的情况。比如<code>translatePigLatin(\"she\")</code>应该返回 \"eshay\"。","testString":"assert.deepEqual(translatePigLatin(\"she\"), \"eshay\", '你的代码应当能够处理第一个元音字母在单词结尾的情况。比如<code>translatePigLatin(\"she\")</code>应该返回 \"eshay\"。');"},{"text":"你的代码应当能够处理单词中不含元音字母的情况。比如<code>translatePigLatin(\"rhythm\")</code>应该返回 \"rhythmay\"。","testString":"assert.deepEqual(translatePigLatin(\"rhythm\"), \"rhythmay\", '你的代码应当能够处理单词中不含元音字母的情况。比如<code>translatePigLatin(\"rhythm\")</code>应该返回 \"rhythmay\"。');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function translatePigLatin(str) {\n  return str;\n}\n\ntranslatePigLatin(\"consonant\");","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/javascript-algorithms-and-data-structures/intermediate-algorithm-scripting/search-and-replace","id":"aa7697ea2477d1316795783b"},"slug":"/javascript-algorithms-and-data-structures/intermediate-algorithm-scripting/pig-latin"}}

